import * as ActionTypes from '../constants/actionTypes';
import * as Logger from '../constants/logCSS';

const initState = {
    isInit: false,
    isDev: false,
    themes: null,
    witnesses: null,
    hearings: null,
    displayHearing: false,
    selectedHearing: null,
    displayVideo: false,
    videoToWatch: '',
    scrollToVideo: false
};

function mainReducer(state = initState, action) {
    let current = Object.assign({}, state);
    switch (action.type) {
        case ActionTypes.INIT_APP:
            current.themes = loadThemes(action.hearingsData);
            current.witnesses = loadWitnesses(action.hearingsData);
            if (action.hearingsData.hearings === undefined) {
                Logger.cl('NO HEARINGS SCHEDULED', Logger.WARNING);
                current.hearings = new Map();
            } else {
                let tmpHearings = new Map(action.hearingsData.hearings);
                current.hearings = new Map();
                for (const hearing of tmpHearings.entries()) {
                    let preppedHearing = hearing[1];
                    preppedHearing.themes = new Map(preppedHearing.themes);
                    current.hearings.set(hearing[0], preppedHearing);
                }
            }
            current.isInit = true;
            current.isDev = action.isDev;
            Logger.cl('EDITOR INITIALIZED', Logger.SUCCESS);
            break;
        case ActionTypes.SELECT_HEARING:
            if (current.hearings.has(action.hearingId)) {
                current.selectedHearing = current.hearings.get(action.hearingId);
                Logger.cl('SELECT HEARING', Logger.SUCCESS);
            } else {
                Logger.cl('HEARING NOT FOUND', Logger.WARNING);
            }
            break;
        case ActionTypes.DESELECT_HEARING:
            current.selectedHearing = null;
            current.scrollToVideo = false;
            Logger.cl('DESELECTED HEARING', Logger.SUCCESS);
            break;
        case ActionTypes.SHOW_HEARING:
            current.displayHearing = true;
            Logger.cl('DISPLAY HEARING', Logger.SUCCESS);
            break;
        case ActionTypes.HIDE_HEARING:
            current.displayHearing = false;
            Logger.cl('HIDE HEARING', Logger.SUCCESS);
            break;
        case ActionTypes.PLAY_VIDEO:
            current.videoToWatch = current.selectedHearing.videos[action.videoToWatch];
            Logger.cl('PLAY VIDEO', Logger.SUCCESS);
            break;
        case ActionTypes.SHOW_HIDE_VIDEO:
            current.displayVideo = action.showVideo;
            if (current.displayVideo) {
                Logger.cl('SHOW VIDEO', Logger.SUCCESS);
            } else {
                Logger.cl('HIDE VIDEO', Logger.SUCCESS);
            }
            break;
        case ActionTypes.SCROLL_TO_VIDEO:
            current.scrollToVideo = true;
            Logger.cl('SCROLLING VIDEO', Logger.SUCCESS);
            break;
        default:
    }
    return current;
}

function loadThemes(hearingsData) {
    let themes;
    if (hearingsData.themes === undefined) {
        Logger.cl('NO THEMES', Logger.WARNING);
        themes = new Map();
    } else {
        themes = new Map(hearingsData.themes);
    }
    themes = new Map([...themes.entries()].sort((a, b) => a[1].localeCompare(b[1])));
    return themes;
}

function loadWitnesses(hearingsData) {
    let witnesses;
    if (hearingsData.witnesses === undefined) {
        Logger.cl('NO WITNESSES', Logger.WARNING);
        witnesses = new Map();
    } else {
        witnesses = new Map(hearingsData.witnesses);
    }
    witnesses = new Map([...witnesses.entries()].sort((a, b) => a[1].lastName.localeCompare(b[1].lastName)));
    return witnesses;
}

export default mainReducer;