import React from 'react';
import { connect } from 'react-redux';
import * as CalendarType from '../constants/calendarTypes';
import { selectHearingAction } from '../actions/actions';
import { deSelectHearingAction } from '../actions/actions';
import { showHearingAction } from '../actions/actions';
import { hideHearingAction } from '../actions/actions';

class Day extends React.Component {
    handleDayClick() {
        if (this.props.selectedHearing === null) {
            this.props.selectHearing(this.props.hearing.timeStamp);
            this.props.showHearing();
        } else {
            if (this.props.selectedHearing.timeStamp === this.props.hearing.timeStamp) {
                this.props.hideHearing();
                setTimeout(() => { this.props.deSelectHearing(); }, 600);

            } else {
                this.props.hideHearing();
                setTimeout(
                    () => {
                        this.props.selectHearing(this.props.hearing.timeStamp);
                        this.props.showHearing();
                    }, 750);
            }
        }
    }

    render() {
        if (this.props.clearDay) {
            return (
                <div className="ClearDay"></div>
            );
        } else {
            let cssClasses = '';
            let today = new Date();
            if (this.props.calendarType === CalendarType.EXHIBITS || this.props.calendarType === CalendarType.CALENDAR) {
                cssClasses = 'DayNoSelection';
            } else {
                cssClasses = 'Day';
            }
            if (this.props.calendarType !== CalendarType.EXHIBITS && this.props.selected) {
                cssClasses += ' SelectedDay';
            } else if (this.props.hearing !== null) {
                if (this.props.hearing.isCancelled) {
                    cssClasses += ' CancelledHearing';
                } else {
                    cssClasses += ' EvidentiaryHearing';
                }
            } else if (this.props.date.getFullYear() === today.getFullYear() && this.props.date.getMonth() === today.getMonth() && this.props.date.getDate() === today.getDate() && this.props.calendarType === CalendarType.EDITOR) {
                cssClasses += ' CurrentDay';
            }
            return (
                <div className={ cssClasses } onClick={ this.handleDayClick.bind(this) }>{ this.props.date.getDate() }</div>
            );
        }
    }
}

const mapStateToProps = (state, ownProps) => {
    let hasHearing = null;
    if (ownProps.date !== undefined && state.hearings.has(ownProps.date.getTime())) {
        hasHearing = state.hearings.get(ownProps.date.getTime());
    }
    return {
        hearing: hasHearing,
        displayHearing: state.displayHearing,
        selectedHearing: state.selectedHearing
    };
}

const mapDispatchToProps = (dispatch) => {
    return {
        selectHearing: (hearingTS) => { dispatch(selectHearingAction(hearingTS)) },
        deSelectHearing: () => { dispatch(deSelectHearingAction()) },
        showHearing: () => { dispatch(showHearingAction()) },
        hideHearing: () => { dispatch(hideHearingAction()) }
    };
}

export default connect(mapStateToProps, mapDispatchToProps)(Day);
