import React from 'react';
import { connect } from 'react-redux';
import AnimateHeight from 'react-animate-height';
import ThemesWitnesses from './ThemesWitnesses';
import { playVideoAction } from '../actions/actions';
import { showHideVideoAction } from '../actions/actions';

const ReactMarkdown = require('react-markdown');

class SelectedHearing extends React.Component {
    constructor(props) {
        super(props);
        this.videoRef = React.createRef();
    }

    handleWatchVideoClick(e) {
        if (this.props.videoToWatch === e.currentTarget.dataset.watch) {
            this.props.showHideVideo(false);
            setTimeout(() => {
                this.props.playVideo('');
            }, 500);
        } else {
            this.props.showHideVideo(true);
            this.props.playVideo(e.currentTarget.dataset.watch);
        }
    }

    render() {
        let hearingTitleDate = ''
        let notifications = [];
        if (this.props.hearing !== null) {
            if (this.props.hearing.notifications !== '') {
                notifications.push(
                    (
                        <div key="Notifications">
                            <h2 className="HearingFormSectionTitle">Notifications</h2>
                            <div className="MarkdownPreview">
                                <ReactMarkdown source={this.props.hearing.notifications} />
                            </div>
                        </div>
                    )
                );
            }
            if (this.props.hearing.isCancelled) {
                hearingTitleDate = new Date(this.props.hearing.timeStamp).format('F j, Y');
                return (
                    <div className="SelectedHearing">
                        <h2 className="HearingFormSectionTitle">{hearingTitleDate} <span style={{ color: 'red', fontWeight: '400' }}>Hearing Cancelled</span></h2>
                        {notifications}
                    </div>
                );
            } else {
                let themesList = [];
                let exhibitsList = [];
                let buildingRoom = '';
                let street = '';
                let city = '';
                let morningSession = '';
                let afternoonSession = '';
                hearingTitleDate = new Date(this.props.hearing.timeStamp).format('F j, Y');
                buildingRoom = this.props.hearing.buildingRoom;
                street = this.props.hearing.street;
                city = this.props.hearing.city;
                morningSession = this.props.hearing.morningSession;
                afternoonSession = this.props.hearing.afternoonSession;
                if (this.props.hearing.themes.size === 0) {
                    themesList.push(<p key="NoThemesScheduled">There are no themes scheduled for this hearing.</p>);
                } else {
                    for (const theme of this.props.hearing.themes.keys()) {
                        themesList.push(<ThemesWitnesses key={theme} themeId={theme}></ThemesWitnesses>);
                    }
                }
                if (this.props.hearing.exhibits.length === 0) {
                    exhibitsList.push(<p key="NoExhibitsEntered">There are no exhibits for this hearing.</p>);
                } else {
                    for (const exhibit of this.props.hearing.exhibits) {
                        if (this.props.isDev) {
                            exhibitsList.push(<p key={exhibit}><a href={'/dataDev/exhibits/' + exhibit}>{exhibit}</a></p>);
                        } else {
                            exhibitsList.push(<p key={exhibit}><a href={'/data/exhibits/' + exhibit}>{exhibit}</a></p>);
                        }
                    }
                }
                let webcastLink = [];
                if (this.props.hearing.timeStamp === new Date(new Date().getFullYear(), new Date().getMonth(), new Date().getDate()).getTime()) {
                    webcastLink = <h3 key="WebcastLink" className="HearingFormSectionTitle" style={{ textAlign: 'center' }}><a href="/webcast-live/" target="_blank">View Live Webcast of this Hearing</a></h3>;
                }
                let timeCss = "LocationTimeInfo";
                let timeNoneDefaultMsg = '';
                if (this.props.hearing.defaultTime === false) {
                    timeCss += ' NoneDefault';
                    timeNoneDefaultMsg = <span key="NoneDefaultTimeMsg"><br /><br />Notice change in time.</span>
                }
                let locationCss = "LocationTimeInfo";
                let locationNoneDefaultMsg = '';
                if (this.props.hearing.defaultAddress === false) {
                    locationCss += ' NoneDefault';
                    locationNoneDefaultMsg = <span key="NoneDefaultLocationMsg"><br /><br />Notice change in location.</span>
                }
                let videoList = [];
                let VideoListSectionCss = "VideoListSection";
                if (this.props.hearing.videos !== undefined && this.props.hearing.videos.length >= 0) {
                    let count = 0;
                    VideoListSectionCss = "";
                    for (const video of this.props.hearing.videos) {
                        videoList.push(<div key={'VideoButton' + count} className="VideoButtonLayout"><div ref={this.videoRef} className="Button RegularButton ButtonMarginLeft ButtonMarginRight" data-watch={video} onClick={this.handleWatchVideoClick.bind(this)}>Watch Video {count + 1}</div></div>);
                        count++;
                    }
                }
                let videoHeight = 0;
                let videoPlayer = <p key="Default"></p>;
                if (this.props.displayVideo) {
                    videoHeight = 'auto';
                }
                if (this.props.videoToWatch !== '') {
                    let queryStr = '';
                    if (this.props.scrollToVideo) {
                        queryStr = '?autoplay=1';
                    }
                    videoPlayer = <div key="vimeoplayer" style={{ padding: '56.25% 0 0 0', position: 'relative' }}><iframe src={'https://player.vimeo.com/video/' + this.props.videoToWatch + queryStr} frameBorder="0" allow="autoplay; fullscreen" allowFullScreen style={{ position: 'absolute', top: '0', left: '0', width: '100%', height: '100%'}}></iframe></div>
                }
                if (this.props.scrollToVideo) {
                    window.scrollTo(0, this.videoRef.current.offsetTop);
                }
                return (
                    <div className="SelectedHearing">
                        <h2 className="HearingFormSectionTitle">{hearingTitleDate}</h2>
                        {webcastLink}
                        <div className="HearingInfo">
                            <p className="CalendarWarning">Those attending must go through a security check to be allowed in the hearing room. Please allow time for this.</p>
                            <p className="CalendarWarning">No food or drinks other than water is allowed in the hearing room. There is a cloak room for coats, umbrellas etc.</p>
                            <p style={{marginTop: '20px'}} className="LocationTimeTitle">Address:</p>
                            <p className={locationCss}>{buildingRoom}<br />{street}<br />{city}{locationNoneDefaultMsg}</p>
                            <p className="LocationTimeTitle">Session Times:</p>
                            <p className={timeCss}>Morning Session: {morningSession}<br />Afternoon Session: {afternoonSession}{timeNoneDefaultMsg}</p>
                            <div className={VideoListSectionCss}>
                                <h2 className="HearingFormSectionTitle">Video List</h2>
                                <div className="VideoListContainer">
                                    {videoList}
                                </div>
                            </div>
                            <AnimateHeight duration={500} height={videoHeight}>
                                {videoPlayer}
                            </AnimateHeight>
                            {notifications}
                            <h2 className="HearingFormSectionTitle">Themes</h2>
                            {themesList}
                            <h2 className="HearingFormSectionTitle">Exhibits</h2>
                            {exhibitsList}
                        </div>
                    </div>
                );
            }
        } else {
            return (
                <div className="SelectedHearing">
                </div>
            );
        }
    }
}

const mapStateToProps = (state) => {
    return {
        hearing: state.selectedHearing,
        isDev: state.isDev,
        displayVideo: state.displayVideo,
        videoToWatch: state.videoToWatch,
        scrollToVideo: state.scrollToVideo
    };
}

const mapDispatchToProps = (dispatch) => {
    return {
        playVideo: (videoToWatch) => { dispatch(playVideoAction(videoToWatch)) },
        showHideVideo: (showVideo) => { dispatch(showHideVideoAction(showVideo)) },
    };
}

export default connect(mapStateToProps, mapDispatchToProps)(SelectedHearing);