import React from 'react';
import { connect } from 'react-redux';

class ThemesWitnesses extends React.Component {
    render() {
        let witnessList = [];
        for (const witnessId of this.props.hearing.themes.get(this.props.themeId)) {
            let prefix = this.props.witnesses.get(witnessId).prefix;
            let firstName = this.props.witnesses.get(witnessId).firstName;
            let lastName = this.props.witnesses.get(witnessId).lastName;
            let title = this.props.witnesses.get(witnessId).title;
            let fullName = '';
            if (prefix !== '') {
                fullName = prefix + ' ';
            }
            fullName += firstName + ' ' + lastName;
            let fullTitle = '';
            if (title !== '') {
                fullTitle = ', (' + title + ')';
            }
            witnessList.push(<p key={fullName + fullTitle} className="WitnessListNames"><strong>{fullName}</strong>{fullTitle}</p>)
        }
        return (
            <div className="HearingTheme">
                <p className="HearingThemeName">{this.props.themes.get(this.props.themeId)}</p>
                {witnessList}
            </div>
        );
    }
}

const mapStateToProps = (state) => {
    return {
        hearing: state.selectedHearing,
        themes: state.themes,
        witnesses: state.witnesses
    };
}

const mapDispatchToProps = (dispatch) => {
    return {
    };
}

export default connect(mapStateToProps, mapDispatchToProps)(ThemesWitnesses);