import * as ActionTypes from '../constants/actionTypes';

export function initAppAction(hearingsData, isDev) {
    return { type: ActionTypes.INIT_APP, hearingsData, isDev }
}

export function selectHearingAction(hearingId) {
    return { type: ActionTypes.SELECT_HEARING, hearingId }
}

export function deSelectHearingAction() {
    return { type: ActionTypes.DESELECT_HEARING }
}

export function showHearingAction() {
    return { type: ActionTypes.SHOW_HEARING }
}

export function hideHearingAction() {
    return { type: ActionTypes.HIDE_HEARING }
}

export function playVideoAction(videoToWatch) {
    return { type: ActionTypes.PLAY_VIDEO, videoToWatch }
}

export function showHideVideoAction(showVideo) {
    return { type: ActionTypes.SHOW_HIDE_VIDEO, showVideo }
}

export function scrollToVideoAction() {
    return { type: ActionTypes.SCROLL_TO_VIDEO }
}

