import React from 'react';
import { connect } from 'react-redux';
import { CarouselProvider, Slider, Slide, ButtonBack, ButtonNext } from 'pure-react-carousel';
import 'pure-react-carousel/dist/react-carousel.es.css';
import AnimateHeight from 'react-animate-height';
import { isMobile } from 'react-device-detect';
import Month from './components/Month';
import SelectedHearing from './components/SelectedHearing';
import * as CalendarType from './constants/calendarTypes';
import { initAppAction } from './actions/actions';
import { selectHearingAction } from './actions/actions';
import { showHearingAction } from './actions/actions';
import { playVideoAction } from './actions/actions';
import { showHideVideoAction } from './actions/actions';
import { scrollToVideoAction } from './actions/actions';

const axios = require('axios');

class App extends React.Component {

  getUrlParameter(name) {
    name = name.replace(/[\[]/, '\\[').replace(/[\]]/, '\\]');
    var regex = new RegExp('[\\?&]' + name + '=([^&#]*)');
    var results = regex.exec(window.location.search);
    return results === null ? '' : decodeURIComponent(results[1].replace(/\+/g, ' '));
  };

  render() {
    if (this.props.isInit) {
      let mainHeight = 0;
      if (this.props.displayHearing) {
        mainHeight = 'auto';
      }
      let calendar = [];
      let endTS = 0;
      for (const hearingTS of this.props.hearings.keys()) {
        endTS = (hearingTS > endTS) ? hearingTS : endTS;
      }
      let startDate = new Date(2020, 0, 1, 0, 0, 0, 0);
      startDate.setDate(1);
      let endDate = new Date(endTS);
      let numMonths = (endDate.getFullYear() - startDate.getFullYear()) * 12;
      numMonths -= startDate.getMonth() + 1;
      numMonths += endDate.getMonth() + 2;  // Adds an extra month to allow for adding new dates beyond the last month.
      numMonths = ((numMonths <= 3) ? 3 : numMonths) + 1;  // Make sure it always has three months.
      let visibleSlides = (isMobile) ? 1 : 3;
      let endOfCarousel = (isMobile) ? numMonths - visibleSlides - 2 : numMonths - visibleSlides - 1;  // Subtract 1 so that the carousel scrolls to the correct month.
      for (let i = 0; i < numMonths; i++) {
        let newDate = new Date(startDate)
        newDate.setMonth(startDate.getMonth() + i);
        calendar.push(<Slide index={i} key={newDate.getTime()}><Month date={newDate} calendarType={CalendarType.CALENDAR}></Month></Slide>);
      }
      let selectedHearingKey = "NoHearing";
      if (this.props.selectedHearing !== null) {
        selectedHearingKey = this.props.selectedHearing.timeStamp;
      }
      let isDevHeader = '';
      if (this.props.isDev) {
        isDevHeader = <h2 style={{ borderRadius: '5px', fontWeight: '800', fontSize: '4.7rem', textAlign: 'center', textTransform: 'uppercase', color: 'white', backgroundColor: '#6200ffc4', padding: '10px', position: 'absolute', top: '150px', left: '50%', textShadow: '0px 0px 20px black', transform: 'rotate(10deg) translate(-35%, 0%)', width: '850px' }}>TEST VERSION</h2>
      }
      return (
        <div id="App">
          {isDevHeader}
          <div className="HearingsCalendarApp">
            <CarouselProvider naturalSlideWidth={287} naturalSlideHeight={302} totalSlides={numMonths} visibleSlides={visibleSlides} currentSlide={endOfCarousel}>
              <Slider>
                {calendar}
              </Slider>
              <ButtonBack><i className="fas fa-chevron-left"></i></ButtonBack>
              <ButtonNext><i className="fas fa-chevron-right"></i></ButtonNext>
            </CarouselProvider>
          </div>
          <AnimateHeight duration={500} height={mainHeight}>
            <SelectedHearing key={selectedHearingKey}></SelectedHearing>
          </AnimateHeight>
        </div>
      );
    } else {
      let isDev = this.getUrlParameter('dev');
      let url = '/data/hearings.json';
      if (isDev === 't') {
        url = '/dataDev/hearings.json';
      }
      axios.get(url)
        .then((res) => {
          this.props.initApp(res.data, isDev);
          let preSelect = this.getUrlParameter('h');
          if (preSelect !== '') {
            let id = parseInt(preSelect);
            if (isNaN(id) === false) {
              this.props.selectHearing(id);
              this.props.showHearing();
            }
          }
          let videoIdQuery = this.getUrlParameter('v');
          if (videoIdQuery !== '') {
            let videoId = parseInt(videoIdQuery);
            if (isNaN(videoId) === false) {
              setTimeout(() => {
                this.props.playVideo(videoId);
                this.props.showHideVideo(true);
                this.props.scrollToVideo();
              }, 500);
            }
          }
        });
      return (
        <div id="App">
        </div>
      );
    }
  }

}

const mapStateToProps = (state) => {
  return {
    isInit: state.isInit,
    isDev: state.isDev,
    hearings: state.hearings,
    selectedHearing: state.selectedHearing,
    displayHearing: state.displayHearing
  };
}

const mapDispatchToProps = (dispatch) => {
  return {
    initApp: (hearingsData, isDev) => { dispatch(initAppAction(hearingsData, isDev)) },
    selectHearing: (hearingId) => { dispatch(selectHearingAction(hearingId)) },
    showHearing: () => { dispatch(showHearingAction()) },
    playVideo: (videoToWatch) => { dispatch(playVideoAction(videoToWatch)) },
    showHideVideo: (showVideo) => { dispatch(showHideVideoAction(showVideo)) },
    scrollToVideo: () => { dispatch(scrollToVideoAction()) }
  };
}

export default connect(mapStateToProps, mapDispatchToProps)(App);
